.application {
	background-color: #2c3c4c;
	background-repeat: no-repeat;
	background-position: center top;
	background-size: 100%;
}

.no-webp .application {
	background-image: 
		url("../img/bg-back-mobile@1x.jpg");

	@media (min-resolution: 144dpi),
	       (min-resolution: 1.5dppx) {

		background-image: 
			url("../img/bg-back-mobile@2x.jpg"); 
	}
}

.webp .application {
	background-image: 
		url("../img/bg-back-mobile@1x.webp");

	@media (min-resolution: 144dpi),
	       (min-resolution: 1.5dppx) {

		background-image: 
			url("../img/bg-back-mobile@2x.webp"); 	
	}
}

.application__slogan {
	width: 240px;
	margin: 0 auto;
	padding: 0 40px;
	padding-top: 92px;
	padding-bottom: 310px;

	font-size: 24px;
	line-height: 31px;
	font-weight: 300;
	color: #ffffff;
	text-align: center;

	background-repeat: no-repeat;
	background-position: left bottom;
	background-size: 258px;
}

.no-webp .application__slogan {
	background-image: 
		url("../img/iphone-hand-mobile@1x.png");

	@media (min-resolution: 144dpi),
	       (min-resolution: 1.5dppx) {

		background-image: 
			url("../img/iphone-hand-mobile@2x.png");
	}
}

.webp .application__slogan {
	background-image: 
		url("../img/iphone-hand-mobile@1x.webp");

	@media (min-resolution: 144dpi),
	       (min-resolution: 1.5dppx) {

		background-image: 
			url("../img/iphone-hand-mobile@2x.webp");	
	}
}

.application__slogan--block,
.application__slogan--nojs {
	padding-top: 18px;
}

.application__wrapper-devices {
	padding: 30px 20px;

	text-align: center;

	background-color: #1d2631;
}

.application__download {
	margin-bottom: 30px;
}

.application__devices {
	margin: auto;
	padding: 0;
	margin-bottom: 19px;

	list-style: none;
}

.application__devices-text {
	margin: 0;

	font-size: 14px;
	line-height: 24px;
	font-weight: 300;
	color: #ffffff;
}

@media (min-width: @tablet-width) {
	.application {
		background-position: center bottom, center top;
		background-size: 700px, 1200px;
	}

	.no-webp .application {
		background-image: 
			url("../img/iphone-hand-tablet@1x.png"),
			url("../img/bg-back-tablet@1x.jpg");

		@media (min-resolution: 144dpi),
		       (min-resolution: 1.5dppx) {

			background-image: 
				url("../img/iphone-hand-tablet@2x.png"),
				url("../img/bg-back-tablet@2x.jpg"); 
		}
	}

	.webp .application {
		background-image: 
			url("../img/iphone-hand-tablet@1x.webp"),
			url("../img/bg-back-tablet@1x.webp");

		@media (min-resolution: 144dpi),
	           (min-resolution: 1.5dppx) {

			background-image: 
				url("../img/iphone-hand-tablet@2x.webp"),
				url("../img/bg-back-tablet@2x.webp");
		}
	}

	.application__wrapper {
		width: 660px;
		margin: 0 auto;
	}

	.application__slogan {
		margin: 0;
		margin-bottom: 33px;
		padding: 0;
		padding-top: 205px;
		padding-left: 70px; 
		width: 200px;

		font-size: 40px;
		line-height: 47px;
	}

	.application__slogan--block,
	.application__slogan--nojs {
		padding-top: 94px;
	}

	.no-webp .application__slogan,
	.webp .application__slogan  {
		background-image: none;
	}

	.application__wrapper-devices {
		padding: 0;
		padding-left: 20px;
		width: 305px;

		background-color: transparent;  
	}

	.application__download {
		margin-bottom: 47px;
		padding: 15px 45px;
	}

	.application__devices {
		margin-bottom: 30px;
	}

	.application__devices-text {
		padding-bottom: 110px;

		font-size: 18px;
	}
}

@media (min-width: @desktop-width) { 
	.application {
		background-position: center top;
		background-size: cover;
	}

	.no-webp .application {
		background-image: 
			url("../img/bg-back-desktop@1x.jpg");

		@media (min-resolution: 144dpi),
	           (min-resolution: 1.5dppx) {

			background-image: 
				url("../img/bg-back-desktop@2x.jpg"); 
		}
	}

	.webp .application {
		background-image: 
			url("../img/bg-back-desktop@1x.webp");

		@media (min-resolution: 144dpi),
           	   (min-resolution: 1.5dppx) {

			background-image: 
				url("../img/bg-back-desktop@2x.webp"); 
		}
	}

	.application__wrapper {
		background-repeat: no-repeat;
		background-position: bottom left 27px;
		background-size: 1050px;
	}

	.no-webp .application__wrapper {
		background-image: 
			url("../img/iphone-hand-desktop@1x.png");

		@media (min-resolution: 144dpi),
	           (min-resolution: 1.5dppx) {

			background-image: 
				url("../img/iphone-hand-desktop@2x.png");
		}
	}

	.webp .application__wrapper {
		background-image: 
			url("../img/iphone-hand-desktop@1x.webp");

		@media (min-resolution: 144dpi),
           	   (min-resolution: 1.5dppx) {

			background-image: 
				url("../img/iphone-hand-desktop@2x.webp");
		}
	}

	.application__wrapper {
		width: 940px;
		padding: 0 10px;
		margin: 0 auto;
	}

	.application__slogan {
		width: 250px;
		margin-bottom: 56px;
		padding-left: 26px;
    	padding-top: 220px;

		font-size: 50px;
		line-height: 58px;
	}

	.application__wrapper-devices {
		padding-left: 0;
	}

	.application__download {
		margin-bottom: 65px;
		padding: 15px 30px;

		font-size: 21px;
	}

	.application__devices {
		margin-bottom: 52px;
	}

	.application__devices-text {
		padding-bottom: 122px;
	}
}